import React from 'react'

const Numbers = (props) => {
    return (
    <ul>
        {props.personsShown.map(person =>
          <li className='personList' key={person.name}>{person.name}{" "}{person.number}
            <button type="submit" onClick={() => props.handleClick(person.id, person.name)}>delete</button>
          </li>
        )}
    </ul>
    )
  }

export default Numbers